/**
* 法规库-法律法规组件
* @module 案件
* @desc 修改添加
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <el-dialog :close-on-click-modal='false' :title="editObj.title" :visible.sync="editObj.show" append-to-body
               width="750px" 
    >
        <el-row :gutter="20" v-loading="loading">
            <el-form :model="form" :rules="rules" class="demo-ruleForm" label-width="100px" ref="ruleForm" >
                <el-col :span="24">
                    <el-form-item label="执法领域：" prop="enforceId">
                        <span>{{form.enforceName}}</span>
                    </el-form-item>
                </el-col>
                <el-col :span="24">
                    <el-form-item label="法规类别：" prop="lawType">
                        <el-select class="selectwidth" placeholder="请选择" v-model="form.lawType">
                            <el-option :key="item.value" :label="item.label" :value="item.value"
                                       v-for="item in lawTypeArr"/>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="24">
                    <el-form-item label="法规名称：" prop="lawName">
                        <el-input v-model="form.lawName"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="24">
                    <el-form-item label="法规内容：" prop="lawContent">
                        <el-input :autosize="{ minRows: 4, maxRows: 40}" type="textarea"
                                  v-model="form.lawContent"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="24">
                    <el-form-item label="颁布单位：" prop="promulgationUnit">
                        <el-input v-model="form.promulgationUnit"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="24">
                    <el-form-item label="颁布时间：" prop="promulgationTime">
                        <el-date-picker class="time-picker" placeholder="颁布时间" type="date"
                                        v-model="form.promulgationTime"></el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="是否生效：" v-if='editObj.type!=2'>
                        <el-switch :active-value="1" :inactive-value="2" v-model="form.isEffective"></el-switch>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="生效时间：" prop="effectiveTime" v-if='form.isEffective==2'>
                        <el-date-picker class="time-picker" placeholder="生效时间" type="date"
                                        v-model="form.effectiveTime"></el-date-picker>
                    </el-form-item>
                </el-col>
            </el-form>
        </el-row>

        <span class="dialog-footer" slot="footer">
                <el-button @click="editObj.show = false">取 消</el-button>
                <el-button @click="submitForm('ruleForm')" type="primary">确 定</el-button>
            </span>
    </el-dialog>

</template>

<script>
    import {apiLawAdd, apiLawGet, apiLawlawLogAdd, apiLawUpdate, apiLefDetailGet} from "../../../../api/case";
    import {formatDate} from "../../../../until/wordBook";

    export default {
        name: "Edit",
        props: {
            editObj: {
                type: Object,
                default: null
            },
            enforceId: {
                type: String,
                default: null
            },
            see: {
                type: Boolean,
                default: false
            },
            rowdata: {
                type: Object,
                default: null
            }
        },
        data() {
            let checkDate = (rule, value, callback) => {
                if (!value && this.form.isEffective == 2) {
                    return callback(new Error('请选择生效日期'));
                } else {
                    callback();
                }
            };
            return {
                loading:true,
                form: {},
                enforceIdArr: [],//执法领域
                dialogVisible: false,
                is_effectiveArr: [
                    {
                        value: 0,
                        label: '未生效'
                    }, {
                        value: 1,
                        label: '已生效'
                    },
                    {
                        value: 2,
                        label: '待生效'
                    }
                ],
                onlyShow: true,
                // form: {
                //     pageSize: 20,
                //     pageNum: 1,
                // },
                lawTypeArr: [{
                    value: '宪法',
                    label: '宪法'
                }, {
                    value: '法律',
                    label: '法律'
                }, {
                    value: '行政法规',
                    label: '行政法规'
                }, {
                    value: '地方性法规',
                    label: '地方性法规'
                }, {
                    value: '自治法规',
                    label: '自治法规'
                }, {
                    value: '经济特区的经济法规',
                    label: '经济特区的经济法规'
                }],

                rules: {
                    enforceId: [
                        {required: true, message: '请选择执法领域', trigger: 'blur'}
                    ],
                    lawName: [
                        {required: true, message: '请输入法规名称', trigger: 'blur'},
                        {min: 1, message: '长度在 1 个字符以上', trigger: 'blur'}
                    ],
                    lawType: [
                        {required: true, message: '请选择法规类别', trigger: 'change'}
                    ],
                    promulgationUnit: [
                        {required: true, message: '请选择颁布单位', trigger: 'blur'}
                    ],
                    promulgationTime: [
                        {required: true, message: '请选颁布时间', trigger: 'blur'}
                    ],
                    lawContent: [
                        {required: true, message: '请输入法规内容', trigger: 'blur'},
                        {min: 3, message: '长度在 3 个字符以上', trigger: 'blur'}
                    ],
                    effectiveTime: [
                        {required: true, message: '请选择生效日期', trigger: 'change'},
                    ]

                }
            }
        },
        methods: {

            submitForm(formName) {

                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.form.isEffective = this.form.isEffective == '' ? 0 : this.form.isEffective
                        this.form.effectiveTime = Date.parse(this.form.effectiveTime);
                        this.form.promulgationTime = Date.parse(this.form.promulgationTime);//颁布时间
                        //添加
                        if (this.editObj.type == 2) {

                            apiLawAdd(this.form).then(res => {
                                this.$emit('getAccountList')
                                this.$message({
                                    type: 'success',
                                    message: '添加成功!'
                                });
                                this.editObj.show = false;
                            })
                        } else if (this.editObj.type == 1) {
                            //编辑

                            apiLawUpdate(this.form).then(res => {
                                this.$emit('getAccountList')
                                this.$message({
                                    type: 'success',
                                    message: '修改成功!'
                                });
                                this.editObj.show = false;
                            })

                        } else if (this.editObj.type == 3) {
                            //修订
                            apiLawlawLogAdd(this.form).then(res => {
                                this.$emit('getAccountList')
                                this.$message({
                                    type: 'success',
                                    message: '版本修订成功!'
                                });
                                this.editObj.show = false;
                            })
                        }
                    } else {
                        return false;
                    }
                });
            },

            initPage() {
                //修改+修订
                if (this.editObj.type != 2) {
                    apiLawGet(this.editObj.editId).then(res => {
                        this.loading=false;
                        let dataArr = res.data;
                        dataArr.invalidTime = '';
                        dataArr.effectiveTime = formatDate(dataArr.effectiveTime, 'yyyy-MM-dd')
                        this.form = dataArr;
                    });

                } else {
                    apiLefDetailGet(this.enforceId).then(res => {
                        this.loading=false;
                        this.form.enforceName = res.data.enforceName;
                        // this.form.lawId='',
                        this.form = {
                            enforceName: res.data.enforceName,
                            enforceId: res.data.enforceId,
                            lawType: '',
                            lawContent: '',
                            isEffective: 1,
                        }
                        this.$nextTick(() => {
                            this.$refs.ruleForm.clearValidate();
                        })

                    });
                }

            }
        },

        watch: {
            'editObj.show'(value) {
                if (value) {
                    this.initPage()

                }
            },

        },

    }
</script>

<style lang="less" scoped>
    .selectwidth {
        width: 100%
    }

    .fontcss {
        font-family: "Microsoft YaHei", "微软雅黑", "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", Arial, sans-serif;
        color: #333;
        font-weight: bold
    }

    .time-picker {
        width: 100%;
    }
</style>
