/**
* 法规库-法律法规组件
* @module 案件
* @desc 查看
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" append-to-body
               width="750px"
    >

        <div class="detail-block" v-if="form" v-loading="loading">
            <h3>{{form.lawName}}</h3>
            <el-divider content-position="left">
                <div class="disabled">生效状态：{{ form.isEffective==0?'待生效':(form.isEffective==1)?'已生效':'待生效' }}</div>
            </el-divider>
            <div class="enforceRight-block">

                <el-form :model="form" class="demo-ruleForm" label-width="100px" ref="ruleForm">
                    <el-form-item label="执法领域：">
                        <span class="fontcss" v-if='enforceIdArr.find(item=>{return item.value == form.enforceId})'>{{enforceIdArr.find(item=>{return item.value == form.enforceId}).label}}</span>
                    </el-form-item>
                    <el-form-item label="法规类别：">
                        <span class="fontcss" v-if='lawTypeArr.find(i=>{return i.value == form.lawType})'>{{lawTypeArr.find(i=>{return i.value == form.lawType}).label}}</span>
                    </el-form-item>
                    <el-form-item label="法规内容：">
                        <span class="fontcss" v-html='form.lawContent'></span>
                    </el-form-item>
                    <el-form-item label="颁布单位：" prop="promulgationUnit">
                        <span class="fontcss">{{form.promulgationUnit}}</span>
                    </el-form-item>
                    <el-form-item label="颁布时间：" prop="promulgationTime">
                        <span class="fontcss" v-if="form.promulgationTime">{{form.promulgationTime | formatDate}}</span>
                    </el-form-item>
                </el-form>
            </div>
            <el-divider content-position="right" v-if="form.effectiveTime">生效时间：{{form.effectiveTime | formatDate}}<span
                    v-if="form.invalidTime">至 {{ form.invalidTime | formatDate}}</span></el-divider>
        </div>
    </el-dialog>

</template>

<script>
    import {apiLawGet, apiLefLisGett} from "../../../../api/case";
    import {formatDate, isEffective} from "../../../../until/wordBook";

    export default {
        name: "seeContent",
        props: {
            editId: {
                type: String,
                default: ''
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ""
            },

        },
        data() {
            return {
                loading:true,
                form: {},
                enforceIdArr: [],//执法领域
                lawTypeArr: [{
                    value: '宪法',
                    label: '宪法'
                }, {
                    value: '法律',
                    label: '法律'
                }, {
                    value: '行政法规',
                    label: '行政法规'
                }, {
                    value: '地方性法规',
                    label: '地方性法规'
                }, {
                    value: '自治法规',
                    label: '自治法规'
                }, {
                    value: '经济特区的经济法规',
                    label: '经济特区的经济法规'
                }],
            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },

            //弹框弹出时获取data给form
            initPage() {
                apiLawGet(this.editId).then(res => {
                    this.loading=false;
                    let dataArr = res.data;
                    dataArr.invalidTime = ''
                    this.form = dataArr;
                    this.form.lawContent = this.form.lawContent.replace(/\n/g, '<br/>')
                })
            },

            //执法领域下拉数据
            enforceIdFunc() {
                apiLefLisGett({pageSize: 20, pageNum: 1}).then(res => {
                    let arr = []
                    res.data.forEach((item) => {
                        let obj = {value: item.enforceId, label: item.enforceName}
                        arr.push(obj)
                    })
                    this.enforceIdArr = arr;
                })
            },
        },
        filters: {
            isEffective(value) {
                return isEffective[value]
            },
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd')
            }
        },
        //监听，如果show为true,则执行initPage()
        watch: {
            'show'(value) {
                if (value) {
                    this.initPage();

                }
            }
        },
        created() {
            this.enforceIdFunc()
        }
    }
</script>

<style lang="less" scoped>
    .concss {
        font-weight: bold
    }

    .fontcss {
        font-family: "Microsoft YaHei", "微软雅黑", "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", Arial, sans-serif;
        color: #333;
        font-weight: 500
    }

    .detail-block {
        font-size:  @fontSize + 4;

        h3 {
            text-align: center;
        }

        .disabled {
            color: #cccccc;
        }

        .enforceCode {
            float: right;
        }

        .enforceRight-block {
            padding: 10px 20px 0 20px;

            .enforceRight {
                text-indent: 2em;
                margin: 10px 0;
            }
        }

        .isDisable {
            padding: 0 20px;
            font-size:  @fontSize ;
            color: #888;
        }
    }
</style>
