/**
* 法规库-法律法规组件
* @module 案件
* @desc 列表
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <lj-search-form :onAdd="onAdd" :searchObj="searchObj" @searchList="searchList"/>
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}"
                  @row-click="seeFunction" border v-loading="loading"
                  size="mini">
            <el-table-column align="center" label="法规类别" prop="lawType" width="150"/>
            <el-table-column align="center" label="法规名称" prop="lawName">
            </el-table-column>
            <el-table-column align="center" label="生效时间" prop="effectiveTime" width="150">
                <template slot-scope="scope">
                    {{scope.row.effectiveTime | formatDate}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="是否生效" prop="isEffective" width="100">
                <template slot-scope="scope">
                    {{scope.row.isEffective | isEffective}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="操作" width="200">
                <template slot-scope="scope">
                    <el-button @click.stop="historyFunction(scope.row)" plain size="mini" type="primary">历史版本
                    </el-button>
                    <el-button @click.stop="changeFunction(scope.row)" plain size="mini" type="warning"
                               v-if="buttonCheck('edit')">修订
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--        分页-->
        <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total"
                       @getData="getAccountList"/>
        <!--        添加和删除弹框-->
        <edit-add :editObj="editObj" :enforceId="enforceId" @getAccountList="getAccountList"></edit-add>

        <history-table :TableObj="TableObj" @getAccountList="getAccountList"></history-table>

        <!-- 查看 -->
        <see-content :editId='seeObj.editId' :show.sync='seeObj.show' :title='seeObj.title'></see-content>
    </div>
</template>

<script>
    import {formatDate, isEffective} from "../../../../until/wordBook";
    import editAdd from './Edit';
    import historyTable from './historyTable';
    import LjSearchForm from '../../../../components/LjSearchForm';
    import LjPagination from '../../../../components/LjPagination';
    import {apiLawSearch} from "../../../../api/case";
    import seeContent from "./seeContent";

    export default {
        name: "lawPage",
        props: ['enforceId'],
        data() {
            return {
                loading:true,
                //搜索组件数据
                searchObj: null,
                form: {
                    pageSize: 20,
                    pageNum: 1,
                    isEffective: 1
                },
                total: 0,
                editObj: {
                    title: '',
                    editId: null,
                    show: false,
                },
                seeObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                TableObj: {
                    title: '',
                    editId: null,
                    show: false,
                },
                list: [],
            }

        },
        components: {
            editAdd, LjSearchForm, LjPagination, historyTable, seeContent
        },
        methods: {

            //查看
            seeFunction(row) {
                this.seeObj.show = true;
                this.seeObj.title = '查看全部';
                this.seeObj.editId = row.clId
            },
            //历史版本弹出
            historyFunction(row) {
                this.TableObj = {
                    editId: row.lawId,
                    title: '查看历史版本',
                    show: true
                }
            },
            //编辑
            editFunction(row) {

                this.editObj = {
                    title: '编辑',
                    show: true,
                    type: '1',
                    editId: row.clId
                }
            },
            //修改版本
            changeFunction(row) {
                this.editObj = {
                    title: '修改法律版本',
                    type: '3',
                    editId: row.clId,
                    show: true
                }
            },
            onAdd() {
                this.editObj.type = '2';
                this.editObj.title = '新增人员';
                this.editObj.show = true;
            },
            //搜索
            searchList(data) {  //data就是接收的this.form数据
                data.enforceId = this.enforceId;
                this.loading=true;
                // this.form = data;
                apiLawSearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                })
            },
            //查询+搜索+分页（分页组件可调用）
            getAccountList() {
                //法律法规搜索
                this.form.enforceId = this.enforceId;
                apiLawSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    let arr = [];
                    res.data.forEach(i => {
                        i.name = i.lawName;
                        i.id = i.lawId;
                        arr.push(i)
                    });
                    this.$emit('upDateTree', arr);
                    this.loading = false;
                })
            },
        },
        filters: {
            isEffective(value) {
                return isEffective[value]
            },
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd')
            }
        },
        watch: {
            'enforceId'() {
                this.getAccountList()
            }
        },
        created() {
            this.getAccountList()
        },
    }
</script>

<style scoped>
    .concss {
        color: #409EFF;
        cursor: pointer;
        font-weight: bold
    }
</style>
