/**
* 法规库-法律法规组件
* @module 案件
* @desc 历史版本
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog :close-on-click-modal='false' :title="TableObj.title" :visible.sync="TableObj.show" append-to-body
                   center
                   width="70%">
            <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}"
                      @row-click="seeFunction" v-loading="loading"
                      border size="mini">
                <el-table-column align="center" label="法规类别" prop="lawType"/>
                <el-table-column align="center" label="法规名称" prop="lawName">
                </el-table-column>
                <el-table-column align="center" label="生效时间" prop="effectiveTime" width="150">
                    <template slot-scope="scope">
                        {{scope.row.effectiveTime | formatDate}}
                    </template>
                </el-table-column>
                <el-table-column align="center" label="是否生效" prop="isEffective">
                    <template slot-scope="scope">
                        {{scope.row.isEffective | isEffective}}
                    </template>
                </el-table-column>
                <el-table-column align="center" label="操作" width="80">
                    <template slot-scope="scope">
                        <el-button @click.stop="editFunction(scope.row)" plain size="mini" type="warning"
                                   v-if='scope.row.isEffective==2'>编辑
                        </el-button>
                    </template>
                </el-table-column>

            </el-table>
            <!-- 分页 -->
            <iPagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total"
                         @getData="getAccountList"></iPagination>
            <!--        添加和删除弹框-->
            <editadd :editObj="editObj" @getAccountList="getAccountList"></editadd>
            <!-- 查看 -->
            <seeContent :editId='seeObj.editId' :show.sync='seeObj.show' :title='seeObj.title'></seeContent>
        </el-dialog>
    </div>
</template>
<script>
    import {apiLawDelete, apiLawSearch} from "../../../../api/case";
    import {formatDate, isEffective} from "../../../../until/wordBook";
    import iPagination from '../../../../components/LjPagination';
    import editadd from './Edit';
    import seeContent from "./seeContent";

    export default {
        name: 'historyTable',
        props: {
            TableObj: {
                type: Object,
                default: null
            },

        },
        data() {
            return {
                loading:true,
                form: {
                    pageSize: 1000,
                    pageNum: 1
                },
                total: 100,
                list: [],
                editObj: {
                    title: '',
                    editId: null,
                    show: false,
                },
                seeObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                rowdata: {},
                see: null

            }
        },
        components: {
            iPagination, editadd, seeContent
        },
        methods: {
            //查看
            seeFunction(row) {
                this.seeObj.show = true;
                this.seeObj.title = '查看全部';
                this.seeObj.editId = row.clId
            },
            //编辑
            editFunction(row) {
                this.editObj = {
                    title: '修改',
                    type: '1',
                    editId: row.clId,
                    show: true
                }
            },
            //删除
            delateFunction(row) {
                this.$confirm('此操作将删除该法律版本, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.loading=true;
                    apiLawDelete(row.clId).then(res => {
                        this.loading=false;
                        this.getAccountList()
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    });
                })
            },
            onAdd() {

            },
            //弹框弹出时获取data给form
            getAccountList() {
                this.form.lawId = this.TableObj.editId;
                apiLawSearch(this.form).then(res => {
                    this.loading=false;
                    this.total = res.total;
                    let dataArr = res.data;
                    this.list = dataArr;
                    this.$emit('getAccountList')
                })
            }
        },
        filters: {
            isEffective(value) {
                return isEffective[value]
            },
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd hh:mm:ss')
            }
        },
        //监听，如果show为true,则执行initPage()
        watch: {
            'TableObj.show'(value) {
                if (value) {
                    this.getAccountList();
                }
            }
        },
    }
</script>
<style lang='less' scoped>

</style>


